#filters {
  padding: 10px;
  min-width: 15vw;
  border: none;
  background-color: transparent;
}

.filter {
  border: none;
  width: 100%;
  padding: 8px;
  font-family: Verdana, Arial, sans-serif;
  background-color: transparent;
  font-size: 12px;

  select {
    option {
      display: flex;
      justify-content: space-between;
      padding-left: 5px;
      padding-right: 15px;
    }

    option:checked {
      background-color: var(--col-selected);
    }
  }
}

._filter-nav-bar {
  top: -8px;
}

._filter-item-off {
  color: var(--col-disabled) !important;
}